<template>
    <div class="container">
        <!--  -->
        <div>
            <div class="citylist-content">
                <index-bar
                    highlight-color="#e54847"
                    :z-index="barIndex"
                    sticky
                    :sticky-offset-top="topDistance"
                    :index-list="indexList"
                >
                    <div v-for="section in citylist" :key="section.title" class="section">
                        <index-anchor :index="section.title"/>
                        <div
                            :class="[section.style==='inline'?'section-body-inline':'section-body']"
                        >
                            <div
                                v-for="city in section.items"
                                :key="city.id"
                                class="section-item"
                                @click="selectCity(city.city_name)"
                            >{{city.city_name}}</div>
                        </div>
                    </div>
                </index-bar>
            </div>
        </div>
    </div>
</template>

<script>
import empty from "@/components/empty.vue";
import { throttle } from "utils/util.js";
import { citys } from "utils/citys.js";
import { mapState, mapMutations } from "vuex";
import { Toast, IndexBar, IndexAnchor, Dialog } from "vant";
export default {
  name: "city-select",
  components: {
    empty,
    IndexBar,
    IndexAnchor
  },
  data() {
    return {
      citylist: [],
      navTop: 0, //,
      navItemHeight: 0, //
      sections: [], //sectionsection
      topDistance: 45,
      barIndex: 0,
      indexList: [],
    };
  },
  created() {
    this.$store.commit('changeTitle',"")
    this.$store.commit('IsBackPage',true)
    this.normalizeCityList(citys);
  },
  methods: {
    //API
    normalizeCityList(citys) {
      let map = {};
      citys.forEach(item => {
        const key = item.city_pre.toUpperCase();
        //
        if (!map[key]) {
          map[key] = {
            title: key,
            items: []
          };
        }
        map[key].items.push(item);
      });
      let list = [];
      for (let [index, value] of Object.entries(map)) {
        list.push(value);
      }
      //
      list.sort((a, b) => a.title.charCodeAt(0) - b.title.charCodeAt(0));
      //
      const hot = {
        title: "",
        style: "inline",
        items: citys.slice(0, 10)
      };
      list.unshift(hot);
      //
      let current = {
        title: "",
        style: "inline",
        items: []
      };
      current.items = [
        {
          city_name: this.$store.state.selectCity
        }
      ];
      list.unshift(current);
      let arrIndex = [];
      list.forEach(item => {
        arrIndex.push(item.title);
      });
      this.indexList = arrIndex;
      this.citylist = list;
    },
    //
    selectCity(cityName) {
      Dialog.confirm({
        title: "",
        message: "IDAPI",
        confirmButtonColor: '#e54847'
      }).then(() => {
        this.$store.commit('selectCity',cityName)
        this.$router.push('/movie')
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  background-color: #ebebeb;
  color: #333;
  font-size: 28px;

  .search-city {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background-color: #f5f5f5;
    padding: 22px 30px;
    box-sizing: border-box;
  }

  .search-input {
    height: 2em;
    background: #fff;
    padding-left: 60px;
    color: #999;
  }
}

.icon-sousuo {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50px;
  transform: translateY(-50%);
  color: #999;
}

/*  */

.result-box {
  min-height: calc(100vh - 100px);
  margin-top: 100px;
  background: #f5f5f5;
}

.result-item {
  height: 88px;
  line-height: 88px;
  margin-left: 30px;
  border-bottom: 1px solid #c8c7cc;
}

/*  */
.section-title {
  padding-left: 30px;
  line-height: 60px;
}

.section-body {
  background-color: #f5f5f5;
  padding-right: 60px;

  .section-item:last-child {
    border: none;
  }

  .section-item {
    height: 88px;
    line-height: 88px;
    margin-left: 30px;
    border-bottom: 1px solid #c8c7cc;
  }
}

.section-body-inline {
  display: flex;
  flex-wrap: wrap;
  background-color: #f5f5f5;
  padding-bottom: 30px;
  padding-left: 30px;

  .section-item {
    min-width: 26%;
    background: #fff;
    margin-top: 30px;
    margin-right: 4%;
    border-radius: 6px;
    line-height: 66px;
    text-align: center;
    box-sizing: border-box;
    border: 1px solid #e6e6e6;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 30px;
  }
}

/*  */

.citylist-nav {
  position: fixed;
  top: 13vh;
  right: 0;
  width: 30px;
  text-align: center;

  > view {
    font-size: 24px;
    line-height: 1.5;
  }
}
</style>